import * as React from 'react';
import { isRequired } from '../core';
import { BlueprintFormMiddlewareProps } from '../share';
import { FormGroup } from '@blueprintjs/core';

export const FormItemTemplateBaseMw: React.ComponentType<BlueprintFormMiddlewareProps> = (props) => {
  const { id, schema, dataPath, next, errors } = props;

  if (typeof schema === 'boolean') return next(props);

  // const error = errors && errors.find(({ dataPath }) => dataPath === id);

  return (
    <FormGroup
      label={schema.title || dataPath[dataPath.length - 1]}
      helperText={schema.description}
      labelInfo={isRequired(props) ? '(required)' : ''}
    >
      {next(props)}
    </FormGroup>
  );
};

export const FormItemTemplateMw: React.ComponentType<BlueprintFormMiddlewareProps> = (props) => {
  const { schema, parent, next } = props;
  if (
    typeof schema === 'boolean' ||
    schema.type === 'object' ||
    schema.type === 'array' ||
    (parent && typeof parent.schema !== 'boolean' && parent.schema.type === 'array')
  )
    return next(props);

  return <FormItemTemplateBaseMw {...props} />;
};

export default FormItemTemplateMw;
